html,
body,
#root {
    height: 100%;
    margin: 0;
    /* Some browser may have some default setting for margin (e.g 8px) */
}


/* took from https://github.com/fireship-io/229-multi-level-dropdown/blob/master/src/index.css */

:root {
    /* --header-bg: hsla(71, 100%, 10%, 1); */
    --header-bg: linear-gradient(0deg, rgba(123, 175, 153, 1) 0%, rgba(70, 98, 76, 1) 100%);
    --header-bg-accent: #484a4d;
    --text-color: #dadce1;
    --nav-size: 60px;
    --header-border: 1px solid #474a4d;
    --header-border-radius: 8px;
    --speed: 500ms;
}

.navbar ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.navbar a {
    color: var(--text-color);
    text-decoration: none;
}

.navbar {
    height: var(--nav-size);
    background: var(--header-bg);
    padding: 0 1rem;
    padding-top: 6px;
    border-bottom: var(--header-border);
}

.navbar-nav {
    max-width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
}

.nav-item {
    width: calc(var(--nav-size) * 0.9);
    display: grid;
    place-items: center;
}

.icon-button {
    --button-size: calc(var(--nav-size) * 0.7);
    width: var(--button-size);
    height: var(--button-size);
    background-color: #484a4d;
    border-radius: 50%;
    display: grid;
    place-items: center;
    transition: filter 300ms;
}

.icon-button.open {
    background: rgb(83, 85, 75);
}

.icon-button.open>svg {
    /* filter: sepia(100%); */
    fill: hsla(71, 100%, 10%, 1)
}

.icon-button:hover {
    filter: brightness(1.2);
}


/* @-moz-document url-prefix() {
    .dropdown {
        transform: translateX(-45%) !important;
    }
} */

.icon-button svg {
    fill: var(--text-color);
    width: 20px;
    height: 20px;
}

.dropdown {
    position: absolute;
    /* top: calc(var(--nav-size) - 2px); */
    top: 58px;
    width: 300px;
    transform: translateX(-45%);
    background: var(--header-bg);
    border: var(--header-border);
    border-radius: var(--header-border-radius);
    padding: 1rem;
    overflow: hidden;
    transition: height var(--speed) ease;
    z-index: 100;
}

.menu {
    width: 100%;
}

.menu-item {
    height: 50px;
    display: flex;
    align-items: center;
    border-radius: var(--header-border-radius);
    transition: background var(--speed);
    padding: 0.5rem;
}

.menu-item .icon-button {
    margin-right: 0.5rem;
}

.menu-item .icon-button:hover {
    filter: none;
}

.menu-item:hover {
    background-color: #525357;
}

.icon-right {
    margin-left: auto;
}

.icon-right svg {
    fill: var(--text-color);
    width: 20px;
    height: 20px;
}


/* primary menu */

.menu-primary-enter {
    position: absolute;
    transform: translateX(-110%);
}

.menu-primary-enter-active {
    transform: translateX(0%);
    transition: all var(--speed) ease;
}

.menu-primary-exit {
    position: absolute;
    /* height: auto; */
}

.menu-primary-exit-active {
    transform: translateX(-110%);
    transition: all var(--speed) ease;
}


/* secondary menu */

.menu-secondary-enter {
    position: absolute;
    transform: translateX(110%);
}

.menu-secondary-enter-active {
    transform: translateX(0%);
    transition: all var(--speed) ease;
}

.menu-secondary-exit {
    /* position: absolute; */
}

.menu-secondary-exit-active {
    transform: translateX(110%);
    transition: all var(--speed) ease;
}